import React from "react"
import { Link } from "gatsby"

import Imtu from "../styles/img/svg/br-mtu-intl.svg"
import Typo from "../styles/img/svg/typography.svg"
import Igift from "../styles/img/svg/br-gift-intl.svg"
import Palette from "../styles/img/svg/palette.svg"
import Buttons from "../styles/img/svg/buttons.svg"
import Receipt from "../styles/img/svg/receipt.svg"
import Pagination from "../styles/img/svg/pagination.svg"

export default class Sidebar extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      expandedMenuItem: null,
      activeMenuItem: null
    }
  }

  icons = {
    Imtu: Imtu,
    Typo: Typo,
    Igift: Igift,
    Palette: Palette,
    Buttons: Buttons,
    Receipt: Receipt,
    Pagination: Pagination
  }

  componentDidMount() {
    let parents = this.props.menuItems.filter(x => x.children).filter(parent => {
      return parent.children.find(c => c.link === this.props.currentLocation)
    })

    if (!parents.length) {
      return
    }

    this.setState({
      expandedMenuItem: "p" + this.props.menuItems.indexOf(parents[0]),
      activeMenuItem: this.props.currentLocation
    })
    console.log(this.props.menuItems.indexOf(parents[0]))
  }

  handleParentClick(index) {
    this.setState({
      expandedMenuItem: "p" + index
    })
  }

  render() {
    return (
      <nav className="sidebar">
        <div className="sidebar-menu">
          <ul className="list-unstyled">
            {
              this.props.menuItems.map((link, index) => {
                if (!link.children) {
                  return (
                    <li key={link.name}>
                      <Link to={link.link} className="text-uppercase" activeClassName="active">{link.name}</Link>
                    </li>
                  )
                }

                let parentClass = this.state.expandedMenuItem === "p" + index ? "sidebar-menu--children expanded" : "sidebar-menu--children"

                return (
                  <li key={"p" + index} className={parentClass}>
                    <span className="text-uppercase fake-link"
                          onClick={this.handleParentClick.bind(this, index)}>{link.name}</span>
                    <ul className="list-unstyled">
                      {
                        link.children.map(child => {
                          let Icon = this.icons[child.icon]
                          return (
                            <li key={child.name}
                                className={child.link === this.state.activeMenuItem ? "text-uppercase active" : "text-uppercase"}>
                              <Link to={child.link} activeClassName="active">
                                <span className="sidebar-menu--icon">
                                  <Icon/>
                                </span>
                                <span>{child.name}</span>
                              </Link>
                            </li>
                          )
                        })
                      }
                    </ul>
                  </li>
                )
              })
            }
          </ul>
        </div>
      </nav>
    )
  }
}