/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import { globalHistory } from "@reach/router"
import { withPrefix } from 'gatsby';

import Navbar from "./Navbar"
import Sidebar from "./Sidebar"
import "../styles/styles.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          menuItems {
            name
            link
            children {
              name
              link
              icon
            }
          }
        }
      }
    }
  `)

  let pathname = globalHistory.location.pathname.replace(/\/$/, '').split("/");
  let prefix = withPrefix("/").replace(/\//g, '');
  let homeClassName = pathname[pathname.length - 1] !== prefix ? "h-100" : ""

  console.log(prefix,pathname[pathname.length - 1],homeClassName);

  return (
    <>
      <div className={"h-100"} className={homeClassName}>
        <Navbar siteTitle={data.site.siteMetadata?.title || `Title`} />
        <div className="container-fluid p-0 full-height-body">
          <Sidebar currentLocation={"/" + pathname[pathname.length - 1]} menuItems={data.site.siteMetadata.menuItems} />
          <div className="content-body">
            { children }
          </div>
        </div>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
